<template>
  <div class="video">
    <div class="container">
      <iframe :src="src" width="1200" height="800" frameborder="0" border="0" marginwidth="0" marginheight="0" scrolling="no"></iframe>
      <div class="url_head">
        <label for="url" class="title">请在下面输入您要播放的vip视频地址：</label><br />
        <input type="text" v-model="url" id="url" placeholder="请再次输入您要解析的会员地址" class="input is-primary is-large">
      </div>
      <div style="padding: 10px 0;">
        <button type="button" @click="removeUrl">清空地址栏</button>
        <button type="button" @click="refresh">刷新</button>
      </div>
    </div>
    <div class="bottom">
    </div>
  </div>
</template>

<script>
export default {
  name: 'video',
  data () {
    return {
      src: '',
      url: '' || 'http://www.iqiyi.com/v_19rr839kro.html',
      selected: 'http://vip.gzzza.com/vip.php?url='
    }
  },
  methods: {
    player () {
      this.src = this.selected + this.url
    },
    removeUrl () {
      this.url = ''
    },
    refresh () {
      location.reload()
    }
  },
  created () {
    this.player()
  },
  watch: {
    selected (value, oldValue) {
      this.src = value + this.url
      console.log(this.src)
    },
    url (value, oldValue) {
      this.src = this.selected + value
      console.log(this.src)
    }
  }
}
</script>



